  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.logo {
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
